// main page elements
body {
	font-size: 14px;
	color: $main;
	font-family: 'Gotham Pro', sans-serif;
	font-weight: 500;
	background: white;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	max-width: 980px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 980px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }
.flex {
	display: flex; }
.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	color: white;
	background: $red;
	border-radius: 17px;
	width: 100%; }
.swiper-button-next, .swiper-button-prev {
	color: #2d195e !important;
	top: 44%; }
.swiper-button-prev, .swiper-container-rtl .swiper-button-next {
	left: 22px; }
.swiper-button-next, .swiper-container-rtl .swiper-button-prev {
	right: 22px; }
.work .swiper-slide {
	margin: 0 1px; }
.Ellipse {
	position: absolute;
	right: 24px;
	top: 204px;
	z-index: 1; }
.tellefone {
	top: 136px;
	right: 15px;
